export const Torneos = memo(() => {

    return (
        <section id="torneos">
        <div className="container">

          <div className="section-header">
            <h3>Torneos</h3>
            <p style={{marginTop:-30,marginBottom:40}}><button type="button" className="btn btn-outline-dark">Ver todos los torneos</button></p>
          </div>

           <div className="row">
            {
              torneos.map(view =>
                (
                  <div className="col-lg-4 col-md-6 col-12 wow bounceInUp" data-wow-duration="1.4s" key={view.id}>
                    <div className="card mb-4 custome-card">
                        <img src={view.img} className="card-img-top" alt="img"/>
                        <div className="card-body text-center">
                            <h5 className="card-title text-truncate text-center">{view.title}</h5>
                            <button type="button" className="btn btn-danger">Ver más</button>
                        </div>
                    </div>
                  </div>
                ))
            }
          </div>
  
        </div>
      </section>
    
    )
})

